import React ,{useState,useEffect}from 'react'
import axios from 'axios'



function TechNews() {
const [news, setNews]= useState(null);

useEffect(()=>{
    NewsApi();
},[])

const NewsApi = async()=>{
    const response =await axios.get(`
    https://newsapi.org/v2/top-headlines?country=in&category=technology&apiKey=3064b93e722b420c9df57954f5d8267f`);

setNews(response.data);
// setNews([...new Set([...news, ...response.data])]);
console.log(news)

}
    return (
        <div class="bg-pink-900  flex flex-col">
          
{
    news && news.articles.map(newsInput=>

 
<div class="pt-4 px-2 flex justify-center items-center">
   
   <div class="max-w-4xl bg-yellow-300  border-2 border-gray-300 p-5 rounded-md tracking-wide shadow-lg">
      <div id="header" class="flex"> 
         <img alt="mountain" class="w-20 rounded-md border-2 border-gray-300" src={newsInput.urlToImage} />
         <div id="body" class="flex flex-col ml-5">
            <h4 id="name" class="text-xl font-semibold mb-2">{newsInput.title}</h4>
            <p id="job" class=" mt-2">{newsInput.description}</p>
            <div class="flex mt-5 flex-col ">
               {/* <img alt="avatar" class="w-6 rounded-full border-2 border-gray-300" src="https://www.pinclipart.com/picdir/middle/327-3277954_by-publishing-with-cc-press-authors-will-gain.png" /> */}
               <p class="ml-3"> Author: {newsInput.author}</p> 
           
             <p><a href={newsInput.url} class=" text-blue-600 hover:text-blue-800 underline visited:text-purple-600 mt-2 ml-3"> Source</a></p>  
               
            </div>
         </div>
      </div>

     
   </div>
 
</div>
 )}
        </div>
    )
}

export default TechNews
